@import url(https://fonts.googleapis.com/css2?family=Share+Tech&family=Share+Tech+Mono&family=Poppins:wght@300&display=swap);
/* GLOBAL STYLES */

:root {
  --main: #282c34;
  --clock: #0049FF;
  --pomodoro-timer: #E40C2B;
  --stopwatch: #30DD00;
  font-size: 16px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Share Tech', sans-serif;
  height: 100vh;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
}

button, 
button:focus,
button:hover {
  background-color: none;
  border: none;
  cursor: pointer;
  outline: none;
}

@media (min-width: 1440px) {
  :root {
    font-size: 1.5vw;
  }
}

/* Main App Component Styles */

.App {
    height: 100vh;
    width: 100%;
    position: relative;
}

.widget {
    height: calc(100% - 20vh);
    width: 100%;
}

/* =================== */

/* Header Styles Start */

header.container {
    background-color: var(--main);
    color: #fff;
    justify-content: space-between;
    height: 10vh;
    min-height: 50px;
    padding: 0 4vw;
}

.logo-title {
    width: 45%;
    justify-content: flex-start;
}

.app-title {
    font-size: 1.2rem;
    font-weight: normal;
    letter-spacing: 1px;
    transition: .3s ease;
}

.app-logo {
    height: 0;
    width: 0;
    border-top: 1rem solid transparent;
    border-bottom: 1rem solid transparent;
    border-left: 1.6rem solid #777;
    margin-right: 0.5rem;
    -webkit-transform-origin: 33% 50%;
            transform-origin: 33% 50%;
    transition: .3s ease;
}

.animate-logo { -webkit-animation: spinLogo .4s ease-in-out; animation: spinLogo .4s ease-in-out; }

.fade-in-text { -webkit-animation: fadeInText .3s ease-in-out; animation: fadeInText .3s ease-in-out; }

.fade-in-widget { -webkit-animation: fadeInWidget .3s; animation: fadeInWidget .3s; }

@-webkit-keyframes spinLogo {
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg);}
}

@keyframes spinLogo {
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg);}
}

@-webkit-keyframes fadeInText {
    0% { opacity: 0; -webkit-transform: translateX(10px); transform: translateX(10px); }
    100% { opacity: 1; -webkit-transform: translateX(0); transform: translateX(0); }
}

@keyframes fadeInText {
    0% { opacity: 0; -webkit-transform: translateX(10px); transform: translateX(10px); }
    100% { opacity: 1; -webkit-transform: translateX(0); transform: translateX(0); }
}

@-webkit-keyframes fadeInWidget {
    0% { opacity: 0; -webkit-transform: scale(0.9); transform: scale(0.9); }
    100% { opacity: 1; -webkit-transform: scale(1); transform: scale(1); }
}

@keyframes fadeInWidget {
    0% { opacity: 0; -webkit-transform: scale(0.9); transform: scale(0.9); }
    100% { opacity: 1; -webkit-transform: scale(1); transform: scale(1); }
}

.btn-settings {
    cursor: pointer;
    font-size: 1.5em;
    transition: .3s ease-in-out;
}

.btn-settings:hover {
    color: rgba(255, 255, 255, 0.5);
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}

.author {
    color: #fff;
    font-family: 'Poppins', 'Share Tech', sans-serif;
    font-size: 0.6rem;
    opacity: 0.7;
    text-align: right;
    text-decoration: none;
    transition: 0.3s ease;
}

.author:hover { opacity: 1; }

/* Header Styles End */

/* =================== */

/* Navbar Styles Start */

nav.container {
    border-top: 0.1rem solid rgba(150, 150, 150, 0.3);
    min-height: 10vh;
    width: 100%;
    padding: 0.3rem;
    position: absolute;
    bottom: 0;
    left: 0;
}

nav.container ul {
    justify-content: space-evenly;
    list-style-type: none;
    height: 100%;
    width: 100%;
    max-width: 60%;
}

nav .btn {
    background-color: transparent;
    color: #555;
    font-family: inherit;
    flex-direction: column;
    min-width: 3.5rem;
}

nav .btn span,
.icon {
    pointer-events: none;
    transition: all .3s ease;
}

nav .btn span {
    font-weight: lighter;
    font-size: 0.8rem;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.icon {
    border-radius: 50%;
    font-size: 1.5rem;
}

.btn:hover .icon {
    color: #fff;
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}

.btn:focus .icon { color: #fff; }

.btn-clock:hover,
.btn-clock:focus { color: var(--clock); }

.btn-clock:hover .icon,
.btn-clock:focus .icon { background-color: var(--clock); }

.btn-pomodoro:hover,
.btn-pomodoro:focus { color: var(--pomodoro-timer); }

.btn-pomodoro:hover .icon,
.btn-pomodoro:focus .icon { background-color: var(--pomodoro-timer); }

.btn-stopwatch:hover,
.btn-stopwatch:focus { color: var(--stopwatch); }

.btn-stopwatch:hover .icon,
.btn-stopwatch:focus .icon { background-color: var(--stopwatch); }

/* Navbar Styles End */

/* =================== */

/* Media Queries Start */

@media (min-width: 1024px) {
    .app-title { font-size: 1.5rem; }
    
    .author { font-size: 0.9rem; }

    .icon { font-size: 1.7rem; } 

    nav .btn span { font-size: 1rem; }    
}

@media (min-width: 365px) {
    .logo-title { width: 52%; }
}

@media (max-width: 476px) {
    nav.container ul { max-width: 100%; }
}

@media (max-height: 476px) and (orientation: landscape) {
    nav.container {
        border-top: none;
        border-right: 0.1rem solid rgba(150, 150, 150, 0.3);
        height: calc(100vh - 50px);
        max-width: 10vw;
    }

    nav.container ul { flex-direction: column; }

    nav .btn span { font-size: 0.75rem; }

    .widget {
        height: calc(100% - 50px);
        width: calc(100% - 10vw);
        position: absolute;
        bottom: 0;
        right: 0;
    }
}

/* =================== */
/* Loader Styles Start */

.loader {
    -webkit-animation: hide-loadscreen 0.5s ease-out 5.5s forwards;
            animation: hide-loadscreen 0.5s ease-out 5.5s forwards;
    background-color: #fff;
    position: fixed;
    left: 0;
    top: 0;
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
    transition: 0.3s ease-out;
    width: 100%;
    height: 100vh;
    z-index: 100;
}

@-webkit-keyframes hide-loadscreen {
    0% {
        opacity: 1;
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    100% {
        opacity: 0;
        -webkit-transform: scale(0);
                transform: scale(0);
    }
}

@keyframes hide-loadscreen {
    0% {
        opacity: 1;
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    100% {
        opacity: 0;
        -webkit-transform: scale(0);
                transform: scale(0);
    }
}

.loader-logo-wrapper {
    -webkit-animation: shift-logo-left 0.5s ease-out 4.5s forwards;
            animation: shift-logo-left 0.5s ease-out 4.5s forwards;
    position: absolute;
    transition: 0.3s ease-out;
}

@-webkit-keyframes shift-logo-left {
    0% { -webkit-transform: translateX(0); transform: translateX(0); }
    100% { -webkit-transform: translateX(-2.6em); transform: translateX(-2.6em); }
}

@keyframes shift-logo-left {
    0% { -webkit-transform: translateX(0); transform: translateX(0); }
    100% { -webkit-transform: translateX(-2.6em); transform: translateX(-2.6em); }
}

.loader-circle {
    -webkit-animation: start-loader 0.3s ease-out 0.3s forwards;
            animation: start-loader 0.3s ease-out 0.3s forwards;
    background-color: var(--main);
    border-radius: 50%;
    content: '';
    position: absolute;
    -webkit-transform: scale(0);
            transform: scale(0);
    width: 5rem;
    height: 5rem;
    z-index: -1;
}

@-webkit-keyframes start-loader {
    0% { -webkit-transform: scale(0); transform: scale(0); }
    100% { -webkit-transform: scale(1); transform: scale(1); }
}

@keyframes start-loader {
    0% { -webkit-transform: scale(0); transform: scale(0); }
    100% { -webkit-transform: scale(1); transform: scale(1); }
}

.loader-circle::after {
    -webkit-animation: scale-inner-circle 2.3s linear .7s forwards;
            animation: scale-inner-circle 2.3s linear .7s forwards;
    background-color: #fff;
    border-radius: 50%;
    content: '';
    position: absolute;
    -webkit-transform: scale(0);
            transform: scale(0);
    width: 4rem;
    height: 4rem;
}

@-webkit-keyframes scale-inner-circle {
    0% { -webkit-transform: scale(0); transform: scale(0); }
    15% { -webkit-transform: scale(1); transform: scale(1);}
    85% { -webkit-transform: scale(1); transform: scale(1);}
    100% { -webkit-transform: scale(0); transform: scale(0); }
}

@keyframes scale-inner-circle {
    0% { -webkit-transform: scale(0); transform: scale(0); }
    15% { -webkit-transform: scale(1); transform: scale(1);}
    85% { -webkit-transform: scale(1); transform: scale(1);}
    100% { -webkit-transform: scale(0); transform: scale(0); }
}

.loader-triangle-group {
    -webkit-animation: spin-triangle-group 1.3s ease-out 3.7s forwards;
            animation: spin-triangle-group 1.3s ease-out 3.7s forwards;
    position: relative;
    left: 0.063rem;
    width: 7.5rem;
    height: 5.5rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}

@-webkit-keyframes spin-triangle-group {
    0% { -webkit-transform: rotate(90deg); transform: rotate(90deg);}
    100% { -webkit-transform: rotate(450deg); transform: rotate(450deg);}
}

@keyframes spin-triangle-group {
    0% { -webkit-transform: rotate(90deg); transform: rotate(90deg);}
    100% { -webkit-transform: rotate(450deg); transform: rotate(450deg);}
}

.loader-timer {
    -webkit-animation: spin-timer-icon 1.3s ease-out 3s forwards;
            animation: spin-timer-icon 1.3s ease-out 3s forwards;
    border-radius: 50%;
    opacity: 0;
    position: absolute;
}

.loader-timer-icon {
    background-color: var(--main);
    border-radius: 50%;
    color: #fff;
    font-size: 5rem;
}

@-webkit-keyframes spin-timer-icon {
    0% {
        opacity: 1;
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    25% {
        opacity: 1;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50% {
        opacity: 1;
        -webkit-transform: scale(1) rotate(0);
                transform: scale(1) rotate(0);
    }
    100% {
        opacity: 1;
        -webkit-transform: scale(1) rotate(360deg);
                transform: scale(1) rotate(360deg);
    }
}

@keyframes spin-timer-icon {
    0% {
        opacity: 1;
        -webkit-transform: scale(0);
                transform: scale(0);
    }
    25% {
        opacity: 1;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    50% {
        opacity: 1;
        -webkit-transform: scale(1) rotate(0);
                transform: scale(1) rotate(0);
    }
    100% {
        opacity: 1;
        -webkit-transform: scale(1) rotate(360deg);
                transform: scale(1) rotate(360deg);
    }
}

.loader-triangle {
    border-left: 1rem solid transparent;
    border-right: 1rem solid transparent;
    opacity: 0;
    position: absolute;
    -webkit-transform-origin: 50% 65%;
            transform-origin: 50% 65%;
    transition: .3s ease;
    width: 0;
    height: 0;
    z-index: 1;
}

.loader-triangle:nth-child(1) {
    -webkit-animation: spin-loader-blue-triangle 1s linear 2.2s forwards;
            animation: spin-loader-blue-triangle 1s linear 2.2s forwards;
    border-bottom: 1.7rem solid var(--clock);
    left: 0;
    bottom: 0;
}

.loader-triangle:nth-child(2) {
    -webkit-animation: spin-loader-red-triangle 1s linear 1.2s forwards;
            animation: spin-loader-red-triangle 1s linear 1.2s forwards;
    border-bottom: 1.7rem solid var(--pomodoro-timer);
    left: 50%;
    top: 0;
    -webkit-transform: translate(-50%, -100%);
            transform: translate(-50%, -100%);
}

.loader-triangle:nth-child(3) {
    -webkit-animation: spin-loader-green-triangle 1s linear 1.7s forwards;
            animation: spin-loader-green-triangle 1s linear 1.7s forwards;
    border-bottom: 1.7rem solid var(--stopwatch);
    right: 0;
    bottom: 0;
}

@-webkit-keyframes spin-loader-blue-triangle {
    0% {
        -webkit-transform: translate(100%, -100%) rotate(0) scale(0.5);
                transform: translate(100%, -100%) rotate(0) scale(0.5);
    }
    10% {
        opacity: 1;
    }
    50% {
        -webkit-transform: translate(-100%, 0) rotate(360deg) scale(1.2);
                transform: translate(-100%, 0) rotate(360deg) scale(1.2);
    }
    100% {
        opacity: 1;
        -webkit-transform: translate(0) rotate(720deg) scale(1);
                transform: translate(0) rotate(720deg) scale(1);
    }
}

@keyframes spin-loader-blue-triangle {
    0% {
        -webkit-transform: translate(100%, -100%) rotate(0) scale(0.5);
                transform: translate(100%, -100%) rotate(0) scale(0.5);
    }
    10% {
        opacity: 1;
    }
    50% {
        -webkit-transform: translate(-100%, 0) rotate(360deg) scale(1.2);
                transform: translate(-100%, 0) rotate(360deg) scale(1.2);
    }
    100% {
        opacity: 1;
        -webkit-transform: translate(0) rotate(720deg) scale(1);
                transform: translate(0) rotate(720deg) scale(1);
    }
}

@-webkit-keyframes spin-loader-red-triangle {
    0% {
        -webkit-transform: translate(-50%, 100%) rotate(0) scale(0.5);
                transform: translate(-50%, 100%) rotate(0) scale(0.5);
    }
    10% {
        opacity: 1;
    }
    50% {
        -webkit-transform: translate(-50%, -200%) rotate(360deg) scale(1.2);
                transform: translate(-50%, -200%) rotate(360deg) scale(1.2);
    }
    100% {
        opacity: 1;
        -webkit-transform: translate(-50%, -100%) rotate(720deg) scale(1);
                transform: translate(-50%, -100%) rotate(720deg) scale(1);
    }
}

@keyframes spin-loader-red-triangle {
    0% {
        -webkit-transform: translate(-50%, 100%) rotate(0) scale(0.5);
                transform: translate(-50%, 100%) rotate(0) scale(0.5);
    }
    10% {
        opacity: 1;
    }
    50% {
        -webkit-transform: translate(-50%, -200%) rotate(360deg) scale(1.2);
                transform: translate(-50%, -200%) rotate(360deg) scale(1.2);
    }
    100% {
        opacity: 1;
        -webkit-transform: translate(-50%, -100%) rotate(720deg) scale(1);
                transform: translate(-50%, -100%) rotate(720deg) scale(1);
    }
}

@-webkit-keyframes spin-loader-green-triangle {
    0% {
         -webkit-transform: translate(-100%, -100%) rotate(0) scale(0.5);
                 transform: translate(-100%, -100%) rotate(0) scale(0.5);
     }
     10% {
         opacity: 1;
     }
     50% {
         -webkit-transform: translate(100%, 0) rotate(360deg) scale(1.2);
                 transform: translate(100%, 0) rotate(360deg) scale(1.2);
     }
     100% {
         opacity: 1;
         -webkit-transform: translate(0) rotate(720deg) scale(1);
                 transform: translate(0) rotate(720deg) scale(1);
     }
 }

@keyframes spin-loader-green-triangle {
    0% {
         -webkit-transform: translate(-100%, -100%) rotate(0) scale(0.5);
                 transform: translate(-100%, -100%) rotate(0) scale(0.5);
     }
     10% {
         opacity: 1;
     }
     50% {
         -webkit-transform: translate(100%, 0) rotate(360deg) scale(1.2);
                 transform: translate(100%, 0) rotate(360deg) scale(1.2);
     }
     100% {
         opacity: 1;
         -webkit-transform: translate(0) rotate(720deg) scale(1);
                 transform: translate(0) rotate(720deg) scale(1);
     }
 }

.loader-text {
    -webkit-animation: animate-loader-text 0.5s ease-out 4.5s forwards;
            animation: animate-loader-text 0.5s ease-out 4.5s forwards;
    font-size: 1.3em;
    margin-left: 1em;
    opacity: 0;
    position: absolute;;
    text-transform: uppercase;
    transition: 0.3s ease-out;
    z-index: -2;
}

@-webkit-keyframes animate-loader-text {
    0% { opacity: 0; -webkit-transform: translateX(0); transform: translateX(0); }
    100% { opacity: 1; -webkit-transform: translateX(2.6em); transform: translateX(2.6em);}
}

@keyframes animate-loader-text {
    0% { opacity: 0; -webkit-transform: translateX(0); transform: translateX(0); }
    100% { opacity: 1; -webkit-transform: translateX(2.6em); transform: translateX(2.6em);}
}

/* =================== */
/* Clock Styles Start */

.clock-widget {
    flex-direction: column;
    justify-content: space-evenly;
    height: 100%;
    width: 100%;
    transition: all .5s ease-in-out;
}

.clock {
    font-size: 16px;
    position: relative;
}

.clock-face {
    border-radius: 50%;
    width: 17em;
    height: 17em;
    margin: auto;
    position: relative;
}

.hour-position,
.minute-position {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -0.5em;
    margin-top: -0.5em;
    text-align: center;
    width: 1em;
}

.hour {
    font-family: 'Share Tech Mono', monospace;
    font-size: 1.2em;
}

.minute-position {
    background-color: rgba(0, 0, 0, 0.4);
    height: 1px;
    width: .5em;
    margin: 0;
    -webkit-transform-origin: left;
            transform-origin: left;
}

.minute-position:nth-of-type(5n) {
    background-color: rgba(0, 0, 0, 0.7);
    height: 1.6px;
    width: .8em;
}

.hand {
    background-color: #000;
    border-radius: 100vmax;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
}

.hour-hand {
    height: 0.5em;
    width: 30%;
    z-index: 1;
}

.min-hand {
    height: 0.45em;
    width: 40%;
    z-index: 2;
}

.hour-hand::after,
.min-hand::after {
    background-color: #fff;
    border-radius: 100vmax;
    content: '';
    height: 35%;
    width: 1.5em;
    position: absolute;
    right: 2%;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

.sec-hand {
    background-color: var(--clock);
    height: 0.12em;
    width: 55%;
    z-index: 3;
    -webkit-transform: translate(-7%, -50%);
            transform: translate(-7%, -50%);
}

.sec-hand::after {
    background-color: #fff;
    border: 0.2em solid var(--clock);
    border-radius: 50%;
    content: '';
    height: 0.3em;
    width: 0.3em;
    position: absolute;
    left: 7%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.date-info { font-size: 1.2rem; }

/* =================== */

/* Clock Media Queries */

@media (max-height: 476px) and (orientation: landscape) {
    .clock-widget {
        flex-direction: row;
        justify-content: center;
    }

    .clock {
        font-size: 12px;
        margin-right: 2rem;
    }
}

@media (min-width: 1080px) {
        .clock { font-size: 1.5vw; }
}

@media (min-width: 1440px) {
    .clock { font-size: 1.6vw; }
}

/* =================== */
/* Pomodoro Styles  */

.pomodoro-widget {
    flex-direction: column;
    overflow: hidden;
    position: relative;
    width: 100%;
    height: 100%;
}

.task-page {
    background-color: #fff;
    opacity: 0;
    position: absolute;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    transition: all 0.3s ease;
    width: 100%;
    height: 100%;
    z-index: 1;
}

.task-page.show {
    -webkit-animation: slide-task-page 0.3s;
            animation: slide-task-page 0.3s;
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

@-webkit-keyframes slide-task-page {
    0% { opacity: 0; -webkit-transform: translateX(-100%); transform: translateX(-100%); }
    100% { opacity: 1; -webkit-transform: translateX(0); transform: translateX(0); }
}

@keyframes slide-task-page {
    0% { opacity: 0; -webkit-transform: translateX(-100%); transform: translateX(-100%); }
    100% { opacity: 1; -webkit-transform: translateX(0); transform: translateX(0); }
}

.task-page-header {
    padding: 0.5em 1em;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
}

.task-page-header h3 {
    font-weight: lighter;
    margin-left: 2em;
}

.back-btn {
    background-color: transparent;
    position: absolute;
    left: 1em;
    top: 0.5em;
}

.back-btn-icon { font-size: 1.7rem; }

.task-settings-form {
    width: 90%;
    height: 80%;
}

.input-container {
    border-bottom: 0.1rem solid rgba(150, 150, 150, 0.3);
    margin-bottom: 1em;
    padding: 1em 0;
    width: 100%;
}

.input-container label { font-size: 1.2em; }

.input-container input {
    border: none;
    width: 100%;
    max-width: 70%;
    text-align: right;
}

.input-container input,
.input-container select {
    font-family: 'Poppins', sans-serif;
    font-size: 1em;
}

.input-container input:focus {
    background-color: transparent;
    outline: none;
}

.input-container select {
    cursor: pointer;
    outline: none;
    padding-left: 0.3em;
    width: 3.5em;
}

.input-container .error {
    color: var(--pomodoro-timer);
    font-size: 0.8em;

}

.input-field { float: right;}

.task-info {
    justify-self: flex-start;
    flex-direction: column;
    justify-content: space-evenly;
    margin: 1em;
    min-height: 15vh;
    padding: 0 1em;
    width: 100%;
}

.current-task {
    font-size: 1.5em;
    font-weight: lighter;
    letter-spacing: 1px;
    margin-bottom: 0.5em;
    text-align: center;
    width: 100%;
}

.pomodoro-meter {
    flex-wrap: wrap;
    list-style-type: none;
    justify-content: space-around;
    width: 100%;
    max-width: 18em;
}

.pomodoro {
    background-color: transparent;
    border-radius: 50%;
    margin: 0.2em;
    width: 1.2em;
    height: 1.2em;
}

.pomodoro-icon {
    color: var(--main);
    font-size: 1.2em;
    transition: 0.3s ease-in;
}

.pomodoro.complete .pomodoro-icon {
    color: var(--pomodoro-timer);
}

.pomodoro.current {
    -webkit-animation: pulse 2s infinite;
            animation: pulse 2s infinite;
    box-shadow: 0 0 0 0 rgba(228, 12, 43, 1);
}

@-webkit-keyframes pulse {
    0% {
        background-color: rgba(228, 12, 43, 1);
        box-shadow: 0 0 0 0 rgba(228, 12, 43, 1);
    }

    70% {
        background-color: transparent;
        box-shadow: 0 0 0 0.5em rgba(228, 12, 43, 0);
    }

    100% {
        box-shadow: 0 0 0 0 rgba(228, 12, 43, 0);
    }
}

@keyframes pulse {
    0% {
        background-color: rgba(228, 12, 43, 1);
        box-shadow: 0 0 0 0 rgba(228, 12, 43, 1);
    }

    70% {
        background-color: transparent;
        box-shadow: 0 0 0 0.5em rgba(228, 12, 43, 0);
    }

    100% {
        box-shadow: 0 0 0 0 rgba(228, 12, 43, 0);
    }
}

.timer-info {
    flex-direction: column;
    flex-grow: 1;
    font-size: 16px;
    justify-content: flex-start;
    position: relative;
    width: 100%;
}

.timer {
    flex-direction: column;
    position: relative;
    width: 18em;
    height: 18em;
    transition: 0.5s;
}

.timer.fall {
    -webkit-transform: translateY(100vh) rotateZ(20deg);
            transform: translateY(100vh) rotateZ(20deg);
    opacity: 0;
}

.pomodoros-complete-msg {
    font-size: 2em;
    position: absolute;
    left: 50%;
    top: 30%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    transition: 0.3s ease-in;
    text-align: center;
    width: 80%;
    opacity: 0;
}

.pomodoros-complete-msg.show {
    opacity: 1;
}

.time-left {
    font-family: 'Share Tech Mono', sans-serif;
    font-size: 4em;
    position: absolute;
}

.progress-ring {
    height: 18em;
    width: 18em;
}

.progress-ring-inner,
.progress-ring-outer {
    fill: none;
    stroke: rgba(40, 44, 52, 0.5);
    stroke-width: 1.1em;
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
    transition: 1s linear;
}

.progress-ring-inner {
    
    stroke: var(--pomodoro-timer);
    stroke-miterlimit: 10;
    stroke-width: 1em;
}

/* Interface Styles */

.controls {
    position: absolute;
    right: 0;
    bottom: 0.5rem;
    width: 100%;
}

.pt-btn-group {
    justify-content: space-evenly;
    width: 100%;
    max-width: 22rem;
}

.pt-start-btn {
    background-color: var(--pomodoro-timer);
    border-radius: 50%;
    box-shadow: 0 0 1px rgba(0,0,0,0.3), 0 3px 6px 1px rgba(0,0,0,0.3);
    width: 3rem;
    height: 3rem;
    transition: all 0.3s ease-in-out;
}

.pt-start-btn:not(.disabled):hover {
    opacity: 0.7;
}

.pt-start-btn.disabled {
    background-color: rgba(40, 44, 52, 0.5);
    cursor: not-allowed;
}

.pt-restart-btn,
.pt-add-btn {
    background-color: transparent;
}

.pt-restart-btn:not(.disabled):hover .pt-restart-icon,
.pt-add-btn:not(.disabled):hover .pt-add-icon {
    color: var(--pomodoro-timer);
}

.icon {
    pointer-events: none;
    transition: 0.3s;
}

.pt-start-icon {
    color: #fff;
    font-size: 2.5rem;
}

.pt-restart-icon,
.pt-add-icon {
    font-size: 1.7rem;
}

.pt-restart-btn:not(.disabled) .pt-restart-icon,
.pt-add-btn:not(.disabled) .pt-add-icon {
    color: var(--main);
}


@media (max-height: 476px) and (orientation: landscape) {
    .pomodoro-widget {
        flex-direction: row-reverse;
        justify-content: space-evenly;
    }

    .task-info {
        height: 50%;
        align-self: flex-start;
    }

    .pomodoro-meter {
        justify-content: center;
    }

    .timer-info {
        font-size: 14px;
        justify-content: center;
        margin-left: 1em;
    }

    .controls {
        width: 50%;
    }
}

@media (min-width: 768px) {
    .task-settings-form {
        width: 50%;
    }
    .task-info {
        flex-direction: row;
        justify-content: space-between;
        margin: 0;
        padding: 0 2.5em;
    }

    .current-task { text-align: left; }

    .input-container input {
        max-width: 60%; 
    }

    .timer-info {
        font-size: 18px;
    }
}

@media (min-width: 1440px) {
    .timer-info {
        font-size: 1.7vw;
    }
}
/* Stopwatch Styles  */

.stopwatch-widget {
    flex-direction: column;
    justify-content: center;
    position: relative;
    height: 100%;
    width: 100%;
}

.stopwatch {
    font-size: 15px;
    font-family: 'Share Tech Mono', sans-serif;
    margin-top: 1em;
    position: relative;
    transition: .5s ease;
    width: 100%;
}

.sw-sec-face,
.sw-min-face {
    background-color: var(--main);
    border-radius: 50%;
    transition: .5s ease;
}

.sw-sec-face {
    margin: auto;
    position: relative;
    width: 18em;
    height: 18em;
}

.sw-min-face {
    width: 7.5em;
    height: 7.5em;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.sw-sec-position,
.sw-min-position,
.sw-sec-marker,
.sw-min-marker {
    position: absolute;
    top: 50%;
    left: 50%;
}

.sw-sec-position,
.sw-min-position {
    color: var(--stopwatch);
    margin-left: -0.5em;
    margin-top: -0.5em;
    width: 1em;
    text-align: center;
}

.sw-sec-marker,
.sw-min-marker {
    background-color: #fff;
    height: 0.05em;
    width: 0.5em;
    -webkit-transform-origin: left;
            transform-origin: left;
}

.sw-sec-marker:nth-of-type(5n),
.sw-min-marker:nth-of-type(3n) {
    width: 0.8em;
}

.sw-min-marker:nth-of-type(15n),
.sw-sec-marker:nth-of-type(25n) {
    background-color: var(--stopwatch);
    height: 0.12em;
    width: 1em;
}

.sw-sec-number {
    font-size: 1.2em;
}

.sw-hand {
    border-radius: 100vmax;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
    -webkit-transform-origin: 0 0;
            transform-origin: 0 0;
}

.sw-min-hand {
    background-color: #000;
    height: 0.35em;
    width: 16%;
    z-index: 1;
    -webkit-transform-origin: 0 50%;
            transform-origin: 0 50%;
}

.sw-min-hand::after {
    background-color: var(--main);
    border-radius: 100vmax;
    content: '';
    height: 25%;
    width: 1em;
    position: absolute;
    right: 4%;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}

.sw-sec-hand {
    background-color: #d30d30;
    height: 0.13em;
    width: 52%;
    z-index: 3;
    -webkit-transform: rotate(-90deg) translate(-7%, -50%);
            transform: rotate(-90deg) translate(-7%, -50%);
}

.sw-sec-hand::after {
    background-color: var(--main);
    border: 0.2em solid #d30d30;
    border-radius: 50%;
    content: '';
    height: 0.3em;
    width: 0.3em;
    position: absolute;
    left: 7%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.total-time {
    font-family: 'Share Tech Mono', sans-serif;
    font-size: 1.6em;
    margin-top: 0.5rem;
    text-align: center;
    width: 100%;
}

/* Lap Info Styles */

.lap-list {
    height: 4.5rem;
    overflow: auto;
    position: relative;
    transition: .3s ease-in-out;
}

.lap {
    -webkit-animation: newLap 0.5s ease-in-out;
            animation: newLap 0.5s ease-in-out;
    border-bottom: 0.1rem solid rgba(150, 150, 150, 0.3);
    justify-content: space-between;
    margin: 0 auto;
    padding: 0.5rem 0;
    height: 33.333%;
    width: 90%;
}

.lap-time {
    text-align: right;
}

.lap:last-child {
    border: none;
}

@-webkit-keyframes newLap {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-0.5em);
                transform: translateY(-0.5em);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes newLap {
    0% {
        opacity: 0;
        -webkit-transform: translateY(-0.5em);
                transform: translateY(-0.5em);
    }
    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

/* Interface Styles */

.interface {
    position: absolute;
    right: 0;
    bottom: 0.5rem;
    width: 100%;
}

.sw-btn-group {
    justify-content: space-evenly;
    width: 100%;
    max-width: 22rem;
}

.sw-start-btn {
    background-color: var(--stopwatch);
    border-radius: 50%;
    box-shadow: 0 0 1px rgba(0,0,0,0.3), 0 3px 6px 1px rgba(0,0,0,0.3);
    width: 3rem;
    height: 3rem;
    transition: all 0.3s ease-in-out;
}

.sw-start-btn:hover {
    opacity: 0.7;
}

.sw-restart-btn,
.sw-lap-btn {
    background-color: transparent;
}

.sw-restart-btn:not(.disabled):hover .icon,
.sw-lap-btn:not(.disabled):hover .icon {
    color: var(--stopwatch);
}

.icon {
    pointer-events: none;
    transition: 0.3s;
}

.sw-start-icon {
    color: #fff;
    font-size: 2.5rem;
}

.sw-restart-icon,
.sw-lap-icon {
    font-size: 1.7rem;
}

.sw-restart-btn:not(.disabled) .icon,
.sw-lap-btn:not(.disabled) .icon{
    color: var(--main);
}

.disabled {
    color: rgba(0,0,0,0.4);
}

